
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import AlertModule from 'vux/src/plugins/alert/module'


import { mapState, mapActions } from 'vuex'
import { callPuExctQryToOICS } from "@/api/api"
export default {
directives: {
TransferDom,
AlertModule
},
name: 'app',
components: {
Loading
},
computed: {
...mapState({




isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction
}),
viewTransition() {
if (!this.direction) return ''
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
}
},
data() {
return {

}
},
mounted() {
this.getDataDictionary();
},
created(){

},
methods: {

getDataDictionary() {
let list = [];
let params = {
params: {
RuleName: "GetDataDictionary",
Parm: { DicType: "47" }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
list = list.concat(data.RspMsg.result.ListItems.ListItem || []);
this.$store.commit('updateDictionary', { dictionary: list })
}
else {
this.showModule("获取业务种类数据失败", data.RspMsg.msg);
}
};
callPuExctQryToOICS(params, callBackSuccess);
},//弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
}
}
}



