import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const homePageMap={
  app:'/index',
  other:'/index'
}

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mhome',
      redirect:{path:homePageMap[process.env.APP_ENV]}
    },//运营内控主页
    {
      path: '/index',
      name: 'index',
      component(resolve){
        require(['@/oics/Index'],resolve)
      }
    },
    {
      path: '/home',
      name: 'home',
      component(resolve){
        require(['@/components/Home'],resolve)
      }
    },
    {
      path: '/demos/echarts',
      name: 'echarts',
      component(resolve){
        require(['@/components/demos/echarts'],resolve)
      }
    },
    {
      path: '/demos/Remind',
      name: 'Remind',
      component(resolve){
        require(['@/components/demos/Remind'],resolve)
      }
    },
    {
      path: '/demos/MSearchVw',
      name: 'MSearchVw',
      component(resolve){
        require(['@/components/demos/MSearchVw'],resolve)
      }
    },
    {
      path: '/demos/MTableVw',
      name: 'MTableVw',
      component(resolve){
        require(['@/components/demos/MTableVw'],resolve)
      }
    },
    {
      path: '/demos/MPanelVw',
      name: 'MPanelVw',
      component(resolve){
        require(['@/components/demos/MPanelVw'],resolve)
      }
    }, {
      path: '/demos/MListVw',
      name: 'MListVw',
      component(resolve){
        require(['@/components/demos/MListVw'],resolve)
      }
    }, {
      path: '/demos/MPageVw',
      name: 'MPageVw',
      component(resolve){
        require(['@/components/demos/MPageVw'],resolve)
      }
    },
    {
      path: '/demos/robot',
      name: 'robot',
      component(resolve){
        require(['@/components/demos/Robot'],resolve)
      }
    },//日常工作跟踪任务列表：track_list
    { 
      path: '/routines_track/track_list',
      name: 'routines_track/track_list',
      component(resolve){
        require(['@/oics/routines_track/track_list'],resolve)
      }
    },//日常工作跟踪任务详情：track_details
    { 
      path: '/routines_track/track_details',
      name: 'routines_track/track_details',
      component(resolve){
        require(['@/oics/routines_track/track_details'],resolve)
      }
    },// 现场检查待办任务:site_inspection_list
    { 
      path: '/site_inspections/site_inspection_list',
      name: 'site_inspections/site_inspection_list',
      component(resolve){
        require(['@/oics/site_inspections/site_inspection_list'],resolve)
      }
    },//现场检查待办任务处理:site_inspection_handle
    { 
      path: '/site_inspections/site_inspection_handle',
      name: 'site_inspections/site_inspection_handle',
      component(resolve){
        require(['@/oics/site_inspections/site_inspection_handle'],resolve)
      }
    }
    ,//预警处理主页：index
    {
      path: '/warning_processing/index',
      name: 'warning_processing/index',
      component(resolve){
        require(['@/oics/warning_processing/index'],resolve)
      }
    },//预警处理列表：processing_list
    {
      path: '/warning_processing/processing_list',
      name: 'warning_processing/processing_list',
      component(resolve){
        require(['@/oics/warning_processing/processing_list'],resolve)
      }
    },//处理详细：processing_details
    {
      path: '/warning_processing/processing_details',
      name: 'warning_processing/processing_details',
      component(resolve){
        require(['@/oics/warning_processing/processing_details'],resolve)
      }
    },
    //查证反馈意见：check_feedback
    {
      path: '/warning_processing/check_feedback',
      name: 'warning_processing/check_feedback',
      component(resolve){
        require(['@/oics/warning_processing/check_feedback'],resolve)
      }
    },//审核预警：verify_ealywarning
    {
      path: '/warning_processing/verify_ealywarning',
      name: 'warning_processing/verify_ealywarning',
      component(resolve){
        require(['@/oics/warning_processing/verify_ealywarning'],resolve)
      }
    },//事后监督：index
    {
      path: '/subsequent_supervision/index',
      name: 'subsequent_supervision/index',
      component(resolve){
        require(['@/oics/subsequent_supervision/index'],resolve)
      }
    },//事后监督：supervision_list
    {
      path: '/subsequent_supervision/supervision_list',
      name: 'subsequent_supervision/supervision_list',
      component(resolve){
        require(['@/oics/subsequent_supervision/supervision_list'],resolve)
      }
    },//录入核查反馈意见：information_entry
    {
      path: '/subsequent_supervision/information_entry',
      name: 'subsequent_supervision/information_entry',
      component(resolve){
        require(['@/oics/subsequent_supervision/information_entry'],resolve)
      }
    },
    {
      path: '/404',
      name: '404',
      component(resolve){
        require(['@/components/404'],resolve)
      }
    },
    {
      path: '*',
      name: '',
      redirect:{path:'/404'}
    }
  ]
})



