import Mock from 'mockjs';
import { LoginUsers, Users } from './user';
import { Reminds } from './dataFactory';

const Datas={}
const pageSize = 10

Datas.get1 = function(config){
    return {a:1,b:2}
}

Datas.get2 = function(config){
    return {c:3,d:4}
}

Datas.getUsers = function(config){
    var users = [];
    for (let i = 0; i < 20; i++) {
        users.push(Mock.mock({
          id: Mock.Random.guid(),
          name: Mock.Random.cname(),
          addr: Mock.mock('@county(true)'),
          'age|18-60': 1,
          birth: Mock.Random.date(),
          sex: Mock.Random.integer(0, 1)
        }));
      }
    return {users:users}
}

Datas.getRemindList = function(config){
    let{ type, pageIndex} = config.params.con
    pageIndex = pageIndex||1
    var reminds = [];
    var startIndex = (pageIndex - 1) * pageSize,
        endIndex = startIndex + pageSize;
    return {reminds:Reminds.filter((d)=>{return d.type === type}).slice(startIndex, endIndex)}
}
Datas.getListTest=function(config){
    let{ type, pageIndex} = config.params.con
    var data = []
    if(pageIndex>2)return []
    for (let index = 0; index < 10; index++) {
        data.push({
            src: Mock.mock('@boolean()')?'./static/images/rabbit.png':'./static/images/astonished.png',
            //fallbackSrc: '@/assets/logo.png',
            title: '背对背信用证业务'+(pageIndex*10+index),
            desc: '背对背信用证业务是指我行客户作为贸易结算的中间商，在从事贸易转单业务过程中，收到境内、外银行开来的',
            url: '/product_form/'+index,
            tag:'热门',
            meta_source:'国际业务结算',
            meta_date:'2018-02-11',
            meta_other:'汪建阳'
        })
    }
    return data
}
Datas.getSelectCon=function(config){
    let{ type, pageIndex} = config.params.con
    var data = []
    if(pageIndex>2)return []
    for (let index = 0; index < 10; index++) {
        data.push({
            approvalName: '待审批'+(pageIndex*10+index),
            custName: '测试'+(pageIndex*10+index),
            approvetype: '背对背信用证业务是指我行客户作为贸易结算的中间商，在从事贸易转单业务过程中，收到境内、外银行开来的',
            url: '/product_form/'+index,
            STATUS:'热门'
        })
    }
    return data
}
export default Datas;


