

import Vue from 'vue'
import FastClick from 'fastclick'

import App from './App'

import router from './router/index'
import Vuex from 'vuex'
import { sync } from 'vuex-router-sync'
Vue.use(Vuex)

import Mock from './mock'

Mock.bootstrap()

console.log('当前APP_ENV:',process.env.APP_ENV)



import CloseDialogsPlugin from 'vux/src/plugins/close-dialogs/index.js'
import BusPlugin from 'vux/src/plugins/bus/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import { getDataDictionary } from "@/common/js/getData"
import 'font-awesome/css/font-awesome.css'

import './assets/css/iconfont.css'


const history = window.sessionStorage
const serviceName = window.sessionStorage.getItem("serviceName");
const _Token1 = window.sessionStorage.getItem("_Token1");
const _empId = window.sessionStorage.getItem("_empId");
const _parameters = window.sessionStorage.getItem("_parameters");
history.clear()
history.setItem("serviceName",serviceName);
history.setItem("_Token1",_Token1);
history.setItem("_empId",_empId);
history.setItem("_parameters",_parameters);
history.setItem("isClickImg",'0');
const store = new Vuex.Store({})
store.registerModule('vux', {
state: {
demoScrollTop: 0,
isLoading: false,
direction: 'forward',
dictionary:[]
},
mutations: {
updateDemoPosition (state, payload) {
state.demoScrollTop = payload.top
},
updateLoadingStatus (state, payload) {
state.isLoading = payload.isLoading
},
updateDirection (state, payload) {
state.direction = payload.direction
},//业务种类数据
updateDictionary(state, payload)
{
state.dictionary = payload.dictionary
}
},
actions: {
updateDemoPosition ({commit}, top) {
commit({type: 'updateDemoPosition', top: top})
}
}
})

Vue.use(LoadingPlugin)
Vue.use(BusPlugin)
Vue.use(ConfirmPlugin)
Vue.use(ToastPlugin, {position: 'middle'})

FastClick.attach(document.body)




Vue.use(CloseDialogsPlugin, router)

sync(store, router)

let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
endTime = Date.now()
})
methods.forEach(key => {
let method = router[key].bind(router)
router[key] = function (...args) {
isPush = true
method.apply(null, args)
}
})


router.beforeEach(function (to, from, next) {


const toIndex = history.getItem(to.path)
const fromIndex = history.getItem(from.path)
if (toIndex) {
if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
store.commit('updateDirection', {direction: 'forward'})
} else {

if (!isPush && (Date.now() - endTime) < 377) {
store.commit('updateDirection', {direction: ''})
} else {
store.commit('updateDirection', { direction: 'reverse' })
}
}
} else {
++historyCount
history.setItem('count', historyCount)
to.path !== '/' && history.setItem(to.path, historyCount)
store.commit('updateDirection', {direction: 'forward'})
}

if (/\/http/.test(to.path)) {
let url = to.path.split('http')[1]
window.location.href = `http${url}`
} else {
next()
}
})

router.afterEach(function (to) {
isPush = false




if (process.env.NODE_ENV === 'production') {


}
})


/* eslint-disable no-new */
new Vue({
store,
router,
render: h => h(App)
}).$mount('#app-box')



