import axios from 'axios';
import data from '../mock/data/data';
import Vue from 'vue';
import Toast from 'vux/src/components/toast/index.vue'

import index from 'vux/src/plugins/ajax';


const tokenSwitch = true




const oicsQueryUrl = tokenSwitch == true ? `/meeting/OtherEsbAction.do` : `http://10.112.17.136:9002/CallPuExctQryToOICS.asmx/GetESBCallPuExctQryToOICS`;




axios.defaults.transformRequest = [
(data, config) => {

var token = sessionStorage.getItem('_Token1') == "null" ? "123" : sessionStorage.getItem('_Token1');
config._Token1 = token;


let p = '';
for (let index in data) {
p += encodeURIComponent(index) + '=' + encodeURIComponent(data[index]) + '&';
}


return p;
}]



axios.interceptors.response.use(response => {
let data = {
ReturnCd: "",
RspMsg: []
};
if (typeof (response.data) === "object") {
if (typeof response.data.ReturnCd !== 'undefined') {
data.ReturnCd = response.data.ReturnCd;
}
if (typeof response.data.RspMsg !== 'undefined') {
data.RspMsg = response.data.RspMsg;
}
return data;

} else if (typeof (response.data) === "string") {
var parser = new DOMParser();
var xmlDoc = parser.parseFromString(response.data, "text/xml")
if (xmlDoc.getElementsByTagName("ReturnCd") != "") {
data.ReturnCd = xmlDoc.getElementsByTagName("ReturnCd")[0].textContent;
}
if (xmlDoc.getElementsByTagName("RspMsg") != "") {
data.RspMsg = JSON.parse(xmlDoc.getElementsByTagName("RspMsg")[0].textContent);
}

return data;
}
}, error => {
return Promise.reject(error.response.data);
})

let callBackError = data => {
new Vue().$vux.toast.show({
text: data.message,
width: '20em',
type: 'warn'
})
};


export const callPuExctQryToOICS = (params, success, error = callBackError) => {

console.log("oicsQueryUrl:", oicsQueryUrl)
if (!tokenSwitch) if (!params.hasOwnProperty("Token1")) params.Token1 = "";
if (!tokenSwitch) if (!params.hasOwnProperty("EmployeeNo")) params.EmployeeNo = "11038329";
if (!params.hasOwnProperty("SrvicName1")) params.SrvicName1 = "OICS";
if (!params.params.hasOwnProperty("IntType")) params.params.IntType = "method";
params.params = JSON.stringify(params.params);

return axios.post(oicsQueryUrl, params).then(success).catch(error)
};


