import Mock from 'mockjs';

var Reminds = [];
var type = {
    menus1:'大额资金变动(100万以上)提醒',
    menus2:'专项理财产品到期提醒',
    menus3:'客户生日提醒'
}
for (const key in type) {
    if (type.hasOwnProperty(key)) {
        const title = type[key];
        for (let i = 0; i < 100; i++) {
            Reminds.push(Mock.mock({
              id: Mock.Random.guid(),
              type:key,
              title:title,
              name: Mock.Random.cname(),
              date:  Mock.Random.date(),
              state: Mock.Random.pick(['未读', '已读'])
            }));
        }
    }
}


export { Reminds };


